.mostly-sane-container {
  position: relative;
  margin: 0px auto;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  z-index: 9000;
}
.image-container {
  position: absolute;
  width: 0px;
  height: 0px;
  border-radius: 50%;
  box-shadow: inset 0 0 0 16px rgba(44, 52, 69, 0.4),
    0 1px 2px rgba(44, 52, 69, 0.1);
  font-family: "Karla", sans-serif;
  animation: birth-for-background 1s ease-out forwards,
    zoom 1s ease-out forwards, move-circles 6s linear forwards infinite 1.5s,
    birth-for-background 1s ease-out 10s reverse forwards;
}
.image-container:after {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  border-radius: 100%;
  background-color: transparent;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: birth-for-border 1s ease-out forwards,
    birth-for-border 1s ease-out 10s reverse forwards;
}

@-webkit-keyframes birth-for-border {
  0% {
    border-width: 0px;
    width: 0px;
    height: 0px;
  }

  50% {
    border-width: 125px;
    width: 0px;
    height: 0px;
  }

  100% {
    border-width: 0px;
    width: 250px;
    height: 250px;
  }
}

@keyframes birth-for-border {
  0% {
    border-width: 0px;
    width: 0px;
    height: 0px;
  }

  50% {
    border-width: 125px;
    width: 0px;
    height: 0px;
  }

  100% {
    border-width: 0px;
    width: 250px;
    height: 250px;
  }
}
@-webkit-keyframes birth-for-background {
  0% {
    width: 0px;
    height: 0px;
  }
  40% {
    width: 250px;
    height: 250px;
  }
  100% {
    width: 250px;
    height: 250px;
  }
}
@keyframes birth-for-background {
  0% {
    width: 0px;
    height: 0px;
  }
  40% {
    width: 250px;
    height: 250px;
  }
  100% {
    width: 250px;
    height: 250px;
  }
}
@-webkit-keyframes move-circles {
  0% {
    transform: translateX(0) scale(1, 1);
    -webkit-filter: none;
            filter: none;
    z-index: 2000;
  }

  25% {
    transform: translateX(-200%) scale(0.98, 0.98);
    -webkit-filter: blur(1px);
            filter: blur(1px);
    z-index: 1000;
  }
  30% {
    z-index: -1000;
  }
  50% {
    transform: translateX(0) scale(0.9, 0.9);
    -webkit-filter: blur(6px);
            filter: blur(6px);
  }
  70% {
    z-index: -1000;
  }
  75% {
    transform: translateX(200%) scale(0.98, 0.98);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    z-index: 1000;
  }
  100% {
    transform: translateX(0) scale(1, 1);
    -webkit-filter: none;
            filter: none;
    z-index: 2000;
  }
}
@keyframes move-circles {
  0% {
    transform: translateX(0) scale(1, 1);
    -webkit-filter: none;
            filter: none;
    z-index: 2000;
  }

  25% {
    transform: translateX(-200%) scale(0.98, 0.98);
    -webkit-filter: blur(1px);
            filter: blur(1px);
    z-index: 1000;
  }
  30% {
    z-index: -1000;
  }
  50% {
    transform: translateX(0) scale(0.9, 0.9);
    -webkit-filter: blur(6px);
            filter: blur(6px);
  }
  70% {
    z-index: -1000;
  }
  75% {
    transform: translateX(200%) scale(0.98, 0.98);
    -webkit-filter: blur(0px);
            filter: blur(0px);
    z-index: 1000;
  }
  100% {
    transform: translateX(0) scale(1, 1);
    -webkit-filter: none;
            filter: none;
    z-index: 2000;
  }
}

